import styled from 'styled-components';

import { colors } from '../../../constants/styleConstants';

const InputLabel = styled.label`
  color: ${colors.textColor};
  display: block;
  font-size: 1.5em;
  margin-bottom: 1rem;
`;

export default InputLabel;



